.request-list{
    width: 400px;
    margin-top: 12px;
    background-color: #ffffff;
    .request-item{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .request-item__right{
            display: flex;
            align-items: center;
        }
    }
    .selected{
        background-color:#f2f3f8;
    }
}

.content {
  flex:1;
  margin-top: 12px;
  margin-left: 20px;
}